@import "@/assets/global.scss";




































































































































































































































































#login {
  @extend .button;
}

#register {
  @extend .button;
}

#facebook {
  @extend .button;

  > div {
    position: relative;
    color: #FFFFFF;
    cursor: pointer;
    user-select: unset;
    background: #4267B2;
    border-radius: 6px;
    width: 100%;
    max-width: 366px;

    > img {
      position: absolute;
      left: 18px;
      top: 50%;
      transform: translate(0, -50%);
    }
  }
}

.disable-btn {
  background: #D2D2D2!important;
}

.google-btn {
  width: 100%;
  padding-left: 24px;
  padding-right: 24px;
  margin-bottom: 16px;
}
